.calculator {
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Calc {
  width: 320px;
  margin: 100px auto;
}
#answer {
  background-color: #e2e2e2;
  padding: 10px;
  font-size: 35px;
  width: 295px;
  height: 45px;
  text-align: right;
  border: 3px solid #333;
}
.button {
  background: #333;
  height: 80px;
  width: 80px;
  color: white;
  font-size: 30px;
  border: 1px solid black;
  float: left;
}
.button:hover {
  background-color: rgb(241, 241, 241);
  color: black;
}
.button1 {
  width: 160px;
}
